body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.text {
  text-align: center;
  align-items: center;

}
.list {
  align-items: center;
  text-align: center;
  text-decoration: none;
  list-style: none;

}

.container {
  align-items: center;
  text-align: center;
}



  .container {
  width: 450px;
  min-height: 500px;
  height: auto;
  border-radius: 5px;
  margin: 2% auto;
  box-shadow: 0 9px 50px hsla(20, 67%, 75%, 0.31);
  padding: 2%;
  background-image: linear-gradient(-225deg, #E3FDF5 50%, #FFE6FA 50%);
}
input {
  width: 200px;
  height: 40px;

  margin-top: 2%;
  padding: 15px;
  
  font-size: 16px;
  font-family: 'Abel', sans-serif;
  color: rgb(red, rgb(32, 39, 32), blue);

  outline: none;
  border: none;

  border-radius: 0px 5px 5px 0px;
  transition: 0.2s linear;
}
input:focus {
  transform: translateX(-2px);
  border-radius: 5px;
}

.checkbox {
  height: 35px;
  margin-top: 15px;
  margin-left: -20%;
}

.checkbox-text {
  font-size: 20px;
  margin-top: -7%;
  margin-left: 11%;  
}

button {
  display: inline-block;
  color: #252537;

  width: 280px;
  height: 50px;

  padding: 0 20px;
  background: #fff;
  border-radius: 5px;

  outline: none;
  border: none;

  cursor: pointer;
  text-align: center;
  transition: all 0.2s linear;

  margin: 7% auto;
  letter-spacing: 0.05em;
}

.password-input {
  border: 1px solid #ccc;
  padding: 8px;
  border-radius: 4px;
  margin-right: 8px;
}

.weak {
  border-color: #ff5555;
  border-width: 5px;
}

.moderate {
  border-color: #ffd700;
  border-width: 5px;
}

.strong {
  border-color: #00cc00;
  border-width: 5px;
}

.strength-text {
  color: #000;
}

.strength-text.weak {
  color: #ff5555;
}

.strength-text.moderate {
  color: #ffd700;
}

.strength-text.strong {
  color: #00cc00;
}